import { NextRequest, NextResponse } from 'next/server';
import connectDB from '@/lib/db';
import User from '@/lib/models/User';
import { verifyOTPSchema } from '@/lib/validations';
import { generateAccessToken, generateRefreshToken } from '@/lib/auth';
import { sendWelcomeEmail } from '@/lib/email';
import { successResponse, errorResponse } from '@/lib/api-response';

export async function POST(request: NextRequest) {
    try {
        await connectDB();

        const body = await request.json();

        // Validate request
        const validated = verifyOTPSchema.safeParse(body);
        if (!validated.success) {
            const { response, status } = errorResponse(validated.error.issues[0].message);
            return NextResponse.json(response, { status });
        }

        const { email, otp } = validated.data;

        // Find user with OTP
        const user = await User.findOne({
            email: email.toLowerCase()
        }).select('+otp +otpExpiry');

        if (!user) {
            const { response, status } = errorResponse('User not found');
            return NextResponse.json(response, { status: 404 });
        }

        if (user.emailVerified) {
            const { response, status } = errorResponse('Email already verified');
            return NextResponse.json(response, { status: 400 });
        }

        if (!user.otp || !user.otpExpiry) {
            const { response, status } = errorResponse('OTP not found. Please request a new one.');
            return NextResponse.json(response, { status: 400 });
        }

        // Check OTP expiry
        if (new Date() > user.otpExpiry) {
            const { response, status } = errorResponse('OTP has expired');
            return NextResponse.json(response, { status: 400 });
        }

        // Verify OTP
        if (user.otp !== otp) {
            const { response, status } = errorResponse('Invalid OTP');
            return NextResponse.json(response, { status: 400 });
        }

        // Mark user as verified
        user.emailVerified = true;
        user.otp = undefined;
        user.otpExpiry = undefined;
        await user.save();

        // Send welcome email
        await sendWelcomeEmail(email, user.name);

        // Generate tokens
        const accessToken = generateAccessToken({
            userId: user._id.toString(),
            email: user.email,
            role: user.role,
        });

        const refreshToken = generateRefreshToken({
            userId: user._id.toString(),
            email: user.email,
            role: user.role,
        });

        // Create response with cookies
        const response = NextResponse.json(
            successResponse(
                {
                    user: {
                        id: user._id,
                        name: user.name,
                        email: user.email,
                        role: user.role,
                    },
                    accessToken,
                    refreshToken,
                },
                'Email verified successfully'
            )
        );

        // Set cookies
        response.cookies.set('accessToken', accessToken, {
            httpOnly: true,
            secure: process.env.NODE_ENV === 'production',
            sameSite: 'lax',
            maxAge: 60 * 60, // 1 hour
        });

        response.cookies.set('refreshToken', refreshToken, {
            httpOnly: true,
            secure: process.env.NODE_ENV === 'production',
            sameSite: 'lax',
            maxAge: 60 * 60 * 24 * 7, // 7 days
        });

        return response;
    } catch (error: any) {
        console.error('OTP verification error:', error);
        const { response, status } = errorResponse('Verification failed. Please try again.');
        return NextResponse.json(response, { status });
    }
}
